body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

a {
  color: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

html,body,.App,#root {
  margin: 0;
  width: 100%;
  height: 100%;
}

nav {
  // Old Browsers
  position: fixed;
  // Modern Browsers
  position: sticky;
  top: 0;
  padding: 10px 20px;
  height: 50px;
  width: 100%;
  background-color: #000;
  display: flex;
  align-items: center;
  z-index: 1000;

  #homepage-link {
    text-decoration: none;
    font-weight: bold;
    font-size: 1.3em;
    color: #fff;

    span {
      color: #ddd;
    }
  }
}

.welcome {
  height: calc(100% - 50px);
  background-color: #ada;
  display: flex;
  flex-direction: row;
  padding-bottom: 25px;

  div {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .text {
      width: auto;
      display: inline-block;

      h1 {
        font-size: 3em;
        margin-bottom: 0;
        font-weight: normal;
      }

      p {
        margin-top: 0;
        margin-bottom: 0;
        font-size: 1.5em;
        font-weight: 600;
        margin-left: 15%;
        white-space: nowrap;
      }
    }

    &.photo-me {
      display: flex;
      align-items: center;
      justify-content: center;

      .photo {
        border-radius: 5000px;

        height: auto;
        width: 100%;
        max-width: 50%;
        pointer-events: none;
        opacity: 0;
        transition-duration: 1s;
        transition-property: opacity;

        &.loaded {
          opacity: 1;
        }
      }
    }
  }
}

.languages.indented-1 {
  margin-left: 4rem;
}

.knowledge {
  background-color: #111;
  padding: 10px 20px;

  .grey {
    color: #666;
  }

  h1 {
    margin-top: 0;
    font-size: 2em;
    color: white;

    &.with-margin {
      margin-top: 1em;
    }
  }

  p {
    color: white;
    font-size: 1.5em;
    margin: 5px 0;
  }

  .languages {
    font-family: monospace;
    font-size: 2em;
    color: white;

    margin-bottom: 0;

    &.comment, .comment {
      color: #666;
    }

    .language-react {
      color: #61dbfb;
    }

    .language-html {
      color: #E34C26;
    }

    .language-css {
      color: #2965f1;
    }

    .language-php {
      color: #787CB5;
    }

    .language-javascript {
      color: #F0DB4F;
    }

    .language-asp\.net {
      color: #004798;
    }

    .language-c\# {
      color: #68217A;
    }

    .language-scss {
      color: #c69;
    }

    .language-python {
      color: #4584b6;
    }

    .language-node {
      color: #3C873A;
    }

    .language-vb\.net {
      color: #004D8D;
    }
  }
}

.projects {
  padding: 10px 0;
  min-height: 100%;
  background-color: #bee;

  .link-text {
    margin: 3px 0;
    font-size: 2em;
    font-family: monospace;
    font-weight: bold;
  }

  h1 {
    font-size: 2em;
    margin: 0;
    margin-left: 20px;
    padding: 0 0 10px;
  }

  .react-parallax {
    height: calc(33vh - 32px);
    min-height: 200px;
    color: white;
    padding: 10px 20px;

    &.project-nftmart, &.project-hetools {
      color: #000;

      h1, p {
        text-shadow: -2px -2px 3px #fff,2px -2px 3px #fff,-2px 2px 3px #fff,2px 2px 3px #fff;
      }
    }

    h1, p {
      text-shadow: -2px -2px 3px #000,2px -2px 3px #000,-2px 2px 3px #000,2px 2px 3px #000;
    }

    h1 {
      margin-left: 0;
      padding-bottom: 0;
    }

    p:first-of-type {
      margin-top: 5px;
      margin-bottom: 0;
    }

    p {
      margin: 0;
    }
  }
}

.contact {
  background-color: #eeb;
  padding: 10px 20px;

  h1 {
    margin: 0;
    font-size: 2em;
  }

  p {
    margin-top: 5px;
    margin-bottom: 0;
    font-size: 1.5em;
  }
}

@media screen and (max-width: 600px) {
  .welcome {
    padding-left: 10px;
    padding-right: 10px;
    flex-direction: column-reverse;
    padding-top: 0;
    justify-content: center;

    div {
      width: 100%;

      .text {
        p {
          margin-left: 10%;
          white-space: normal;
        }
      }
    }

    div.photo-me .photo {
      width: 100%;
      max-width: 90%;
    }
  }
}